<template>
  <div>
    <div class="container">
      
    <ul class="collection with-header">
        <li class="collection-header "><h4 >Classes</h4></li>
        
    
    <dispclasses
      v-for="item in posts"
      v-bind:classname="item"
      v-bind:key="item.id">
    </dispclasses>
        
    
     </ul>
    </div>
    

   
      
    
  




    
   
    
